html {
  font-size: 100%;
  font-family: "Nunito Sans", sans-serif;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
}

ol,
ul {
  list-style: none;
}

img {
  pointer-events: none;
  user-select: none;
}

:root {
  --header-title--light: #fff;
  --main-page--light: #FAFAFA;
  --text--light: #000;
  --header-title--dark: #2B3743;
  --main-page--dark: #202D36;
  --text--dark: #fff;
  --text--hover: #1d91d5eb;
  --header-title: var(--header-title--light);
  --main-page: var(--main-page--light);
  --text: var(--text--light);
}
:root > body.light {
  --header-title: var(--header-title--light);
  --main-page: var(--main-page--light);
  --text: var(--text--light);
}
:root > body.dark {
  --header-title: var(--header-title--dark);
  --main-page: var(--main-page--dark);
  --text: var(--text--dark);
}

.center, .filter-box, .search-box, main .single-country .single-country__border .border-item-container, main .single-country .btn--back, .header > .btn-toggle, .header {
  display: flex;
  align-items: center;
}

.header {
  justify-content: space-between;
  background-color: var(--header-title);
  color: var(--text);
  height: 5rem;
  padding: 0rem 0.9375rem;
  transition: all 1s ease-out;
  border-bottom: 0.1px solid rgba(0, 0, 0, 0.1);
}
@media (min-width: 46.25rem) {
  .header {
    padding: 0rem 5rem;
  }
}
.header > h1 {
  font-weight: 800;
  font-size: 0.875rem;
}
@media (min-width: 46.25rem) {
  .header > h1 {
    font-size: 1.5rem;
  }
}
.header > .btn-toggle {
  gap: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: 0.4s ease;
  font-size: 0.75rem;
}
.header > .btn-toggle:hover {
  color: var(--text--hover);
}
@media (min-width: 46.25rem) {
  .header > .btn-toggle {
    font-size: 1rem;
  }
}

main {
  background-color: var(--main-page);
  transition: all 1s ease-out;
  padding: 1.5rem 12vw;
  min-height: calc(100vh - 80px);
}
@media (min-width: 46.25rem) {
  main {
    padding: 2.8125rem 5rem;
  }
}
@media (min-width: 46.25rem) {
  main .all-countries > .tools {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
main .single-country {
  display: none;
}
@media (min-width: 46.25rem) {
  main .single-country {
    grid-template-columns: 1fr 0.8fr;
    grid-template-areas: "back ." "img info" "img border";
  }
  main .single-country .btn--back {
    grid-area: back;
  }
  main .single-country img {
    grid-area: img;
    margin-right: 1.5rem;
  }
  main .single-country .single-country__info {
    grid-area: info;
    align-self: center;
  }
  main .single-country .single-country__border {
    grid-area: border;
    align-self: center;
  }
}
main .single-country .btn--back {
  justify-content: center;
  gap: 12px;
  height: 2rem;
  width: 6.5625rem;
  margin-top: 1.125rem;
  margin-bottom: 4rem;
  box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.2);
  background-color: var(--header-title);
  color: var(--text);
  border-radius: 4px;
  transition: background-color 1s ease-out, color 0.4s ease;
  cursor: pointer;
}
@media (min-width: 46.25rem) {
  main .single-country .btn--back {
    height: 2.625rem;
    width: 8.5rem;
    margin-top: 2rem;
    margin-bottom: 5rem;
  }
}
main .single-country .btn--back:hover {
  color: var(--text--hover);
}
main .single-country .btn--back p {
  font-size: 0.875rem;
  font-weight: 300;
}
@media (min-width: 46.25rem) {
  main .single-country .btn--back p {
    font-size: 1rem;
  }
}
main .single-country img {
  width: 100%;
  object-fit: cover;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}
@media (min-width: 46.25rem) {
  main .single-country img {
    max-width: 35rem;
  }
}
@media (max-width: 46.1875em) {
  main .single-country .single-country__info {
    margin-top: 12vw;
  }
  main .single-country .single-country__info > p:not(:last-child) {
    margin-bottom: 0.375rem;
  }
  main .single-country .single-country__info > p:nth-child(6) {
    margin-bottom: 2rem;
  }
}
@media (min-width: 46.25rem) {
  main .single-country .single-country__info h2 {
    font-size: 1.5rem;
  }
  main .single-country .single-country__info p {
    font-size: 1rem;
  }
}
main .single-country .single-country__border {
  margin-top: 2rem;
}
@media (min-width: 46.25rem) {
  main .single-country .single-country__border {
    display: flex;
    gap: 12px;
  }
}
main .single-country .single-country__border h3 {
  color: var(--text);
  transition: all 1s ease-out;
  font-weight: 600;
  font-size: 0.875rem;
}
@media (max-width: 46.1875em) {
  main .single-country .single-country__border h3 {
    margin-bottom: 8px;
  }
}
main .single-country .single-country__border .border-item-container {
  flex-wrap: wrap;
  gap: 12px;
}
main .single-country .single-country__border .border-item-container span {
  box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.2);
  background-color: var(--header-title);
  color: var(--text);
  font-size: 0.875rem;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  transition: 0.4s ease;
  cursor: pointer;
}
main .single-country .single-country__border .border-item-container span:hover {
  transform: scale(0.98);
  color: var(--text--hover);
}
@media (min-width: 46.25rem) {
  main .single-country .single-country__border .border-item-container span {
    font-size: 1rem;
  }
}

.search-box {
  background-color: var(--header-title);
  border-radius: 4px;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
  padding: 0rem 1.875rem;
  height: 3.125rem;
  transition: all 1s ease-out;
}
@media (min-width: 46.25rem) {
  .search-box {
    width: 50vw;
    max-width: 30rem;
    height: 3.5rem;
  }
}
.search-box input {
  outline: none;
  border: none;
  background-color: transparent;
  width: 100%;
  font-size: 0.75rem;
  color: var(--text);
  transition: all 1s ease-out;
}
.search-box input::placeholder {
  color: var(--text);
}
@media (min-width: 46.25rem) {
  .search-box input {
    font-size: 1rem;
  }
}
.search-box .search-icon {
  margin-right: 1.5rem;
  --ionicon-stroke-width: 62px;
  cursor: pointer;
  color: var(--text);
}
.search-box .search-icon:hover {
  color: var(--text--hover);
  transition: 0.4s ease;
}

.filter-box {
  background-color: var(--header-title);
  border-radius: 4px;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
  width: 12.5rem;
  height: 3.5rem;
  position: relative;
  justify-content: space-between;
  padding: 0 1.125rem;
  cursor: pointer;
  transition: all 1s ease-out;
}
.filter-box::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.375rem;
  cursor: pointer;
  top: 100%;
  left: 0;
}
@media (max-width: 46.1875em) {
  .filter-box {
    margin-top: 2.5rem;
  }
}
.filter-box:hover > ul {
  display: block;
}
.filter-box p {
  color: var(--text);
  font-size: 0.75rem;
  font-weight: 600;
}
@media (min-width: 46.25rem) {
  .filter-box p {
    font-size: 1rem;
  }
}
.filter-box .filterIcon {
  --ionicon-stroke-width: 62px;
  color: var(--text);
}
.filter-box .filterIcon.clear:hover {
  transition: 0.4s ease;
  color: var(--text--hover);
}
.filter-box__dropdown {
  display: none;
  position: absolute;
  background-color: var(--header-title);
  color: var(--text);
  border-radius: 4px;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
  padding: 1rem 1.125rem;
  width: 12.5rem;
  left: 0;
  top: 108%;
  z-index: 100;
  transition: background-color 1s ease-out;
}
.filter-box__dropdown .filter-box__item {
  font-size: 0.75rem;
  border-bottom: 0px;
  transition: 0.4s ease;
}
@media (min-width: 46.25rem) {
  .filter-box__dropdown .filter-box__item {
    font-size: 1rem;
  }
}
.filter-box__dropdown .filter-box__item:hover {
  color: var(--text--hover);
}
.filter-box__dropdown .filter-box__item:not(:last-child) {
  margin-bottom: 0.5rem;
}

.countries {
  margin-top: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 40px;
}

.country {
  border-radius: 6px;
  box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  cursor: pointer;
  height: 21.25rem;
  width: 100vw;
  background-color: var(--header-title);
  transition: background-color 1s ease-out, transform 0.4s ease;
}
@media (min-width: 46.25rem) {
  .country {
    width: 15.625rem;
  }
}
.country:hover {
  transform: translateY(-10px);
}
.country__flag {
  height: 10rem;
  width: 100%;
  object-fit: cover;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}
.country__info {
  padding: 24px 28px;
}
.country__info > p:not(:last-child) {
  margin-bottom: 0.25rem;
}
.country__name {
  font-size: 18px;
  color: var(--text);
  font-weight: 800;
  margin-bottom: 1rem;
}
.country__info__item {
  font-size: 14px;
  color: var(--text);
  font-weight: 600;
}
.country__info__item span {
  font-weight: 300;
}